.Menu {
  display: inline-block;
  width: 100%;
  position: relative;
}
.menuStyle {
  width: 100%;
  background-color: transparent;
  list-style-type: none;
  text-align: center;
  overflow: hidden;
  margin: auto;
}
.listItems {
  width: 32%;
  float: left;
  align-items: center;
  background-color: transparent;
}
.menuButton {
  color: white;
  background-image: linear-gradient(to right, #6b66af, #c9775d);
  padding: 2% 30% 2% 30%;
  border: none;
  color: "#DAD8D8";
}
.menuButton:hover {
  color: #30475e;
}
.title {
  color: white;
  text-align: center;
  padding: 1% 0 0 0;
  font-size: 600%;
}
.main {
  /* background-image: linear-gradient(#6b66af, #c9775d); */
  background-color: transparent;
  height: 100%;
  width: 100%;
  font-family: "Skia Light";
}
.titleHr {
  width: 25%;
  height: 1px;
  border-top: 1px solid #ccc;
  padding: 0 25% 0 25%;
}
.mainContent {
  /* background-image: linear-gradient(#6b66af, #c9775d); */
  background-color: transparent;
  width: 80%;
  padding: 5%;
  margin: 0 auto;
  color: white;
}

/* -------------------------- about me page ------------------------- */
h2 {
  padding: 0%;
  margin: auto;
}
.headlineHr {
  margin: 0;
  float: left;
  width: 20%;
  height: 1px;
  border-top: 1px solid #ccc;
}
.aboutContent {
  padding-top: 5%;
  padding-bottom: 5%;
}
p {
  line-height: 1.9em;
}
.skills {
  float: left;
  align-items: center;
  margin: 0;
  position: relative;
}
#middleSkills {
  padding: 0 19.7% 0 19.7%;
}
.aboutSkillsContainer {
  padding-top: 5%;
  display: inline-block;
  width: 100%;
  height: 100%;
}
/* --------------------------sorting algorithms buttons------------------------- */
#algoMain {
  height: 100%;
  position: relative;
}
.algoList {
  padding: 0;
  background-color: transparent;
  list-style-type: none;
  text-align: center;
  overflow: hidden;
  margin: auto;
}
.algoListItem {
  /* display: inline-block; */
  width: 20%;
  float: left;
  align-items: center;
}
.algoListButtons {
  background-color: transparent;
  border: none;
  color: white;
}
.algoListButtons:hover {
  border-bottom: 1px solid #fcaf45;
}
.algoListButtons:disabled {
  color: gray;
}
/* --------------------------sorting algorithms content ------------------------- */
.sortingMain {
  width: 100%;
  height: 100%;
  position: relative;
}
.bar {
  width: 2px;
  background-color: white;
  display: inline-block;
  margin: 0 1px;
}
/* --------------------------sorting algorithms content ------------------------- */
